@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-medium.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-semi-bold.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-semi-bold.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  scroll-behavior: smooth;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

@media (max-width: 767px) {
  .screen-lock {
    overflow: hidden;
  }
  .screen-lock::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    display: block;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 43, 102, 0.5);
  }
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.advantages {
  position: relative;
  min-height: 562px;
}
@media (max-width: 1023px) {
  .advantages {
    min-height: 588px;
  }
}
@media (max-width: 767px) {
  .advantages {
    min-height: 997px;
  }
}

.advantages__wrapper {
  margin: 0 auto;
  padding: 110px 0;
  max-width: 1160px;
}
@media (max-width: 1023px) {
  .advantages__wrapper {
    padding: 90px 0;
  }
}
@media (max-width: 767px) {
  .advantages__wrapper {
    padding: 57px 0;
  }
}

.advantages__wrapper h2 {
  margin: 0 0 49px 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  text-align: center;
}
@media (max-width: 1023px) {
  .advantages__wrapper h2 {
    font-size: 26px;
    line-height: 39px;
  }
}
@media (max-width: 767px) {
  .advantages__wrapper h2 {
    margin: 0 0 30px 0;
    font-size: 24px;
    line-height: 36px;
  }
}

.advantages__wrapper ul {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .advantages__wrapper ul {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .advantages__wrapper ul {
    grid-template-columns: 1fr;
  }
}

.advantages__wrapper li {
  position: relative;
  padding: 80px 30px 30px 30px;
  height: 100%;
  min-height: 241px;
  background-color: #f9fbfd;
}
@media (max-width: 1023px) {
  .advantages__wrapper li {
    padding: 70px 15px 20px 20px;
    min-width: 206px;
  }
}
@media (max-width: 767px) {
  .advantages__wrapper li {
    min-width: 290px;
  }
}

.advantages__wrapper svg {
  position: absolute;
  top: 30px;
  left: 30px;
}
@media (max-width: 1023px) {
  .advantages__wrapper svg {
    top: 26px;
    left: 20px;
  }
}

@media (max-width: 1023px) {
  .advantages__item--liner svg {
    width: 21px;
    height: 26px;
  }
  .advantages__item--check svg {
    width: 27px;
    height: 26px;
  }
  .advantages__item--boat svg {
    width: 36px;
    height: 26px;
  }
}

.advantages__wrapper h3 {
  margin: 0 0 10px 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}
@media (max-width: 1023px) {
  .advantages__wrapper h3 {
    line-height: 25.2px;
  }
}
@media (max-width: 767px) {
  .advantages__wrapper h3 {
    margin: 0 0 9px 0;
  }
}

.advantages__wrapper p {
  margin: 0;
  font-size: 14px;
  line-height: 19.6px;
}
@media (max-width: 1023px) {
  .advantages__wrapper p {
    line-height: 20px;
  }
}

@media (min-width: 375px) {
  .advantages__wrapper br {
    display: none;
  }
}

.advantages__image-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #e3ecf6;
  z-index: -1;
}

.advantages img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  opacity: 10%;
}
@media (max-width: 767px) {
  .advantages img {
    object-position: 0;
  }
}

.card {
  min-height: 386px;
  color: #f9fbfd;
  perspective: 1200px;
}
.card.focus-visible:focus {
  outline: none;
}

.card__flipper {
  position: relative;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: 0.3s linear;
}

.card:hover .card__flipper,
.card:focus-within .card__flipper {
  transform: rotateY(180deg);
}

.card h3 {
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}

.card__front,
.card__back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  overflow: hidden;
}

.card__front {
  background-color: #011c40;
  z-index: 2;
  transform: rotateY(0deg);
}
.card__front::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 50%;
  background: linear-gradient(180deg, rgba(1, 28, 64, 0) 0%, #011c40 100%);
}

.card__front img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: 1023px) {
  .card--astoria img {
    object-position: 58% 50%;
  }
}
@media (max-width: 767px) {
  .card--astoria img {
    object-position: 57% 50%;
  }
}

@media (max-width: 1023px) {
  .card--elaine img,
.card--minion-1 img {
    object-position: 51% 50%;
  }
}
@media (max-width: 767px) {
  .card--elaine img,
.card--minion-1 img {
    object-position: 52% 50%;
  }
}

@media (max-width: 767px) {
  .card--odysseus img {
    object-position: 37% 50%;
  }
}

@media (max-width: 767px) {
  .card--leonor img {
    object-position: 58% 50%;
  }
}

.card__front h3 {
  position: absolute;
  bottom: 0;
  left: 0;
  margin: 0;
  padding: 23px 30px;
  z-index: 3;
}
@media (max-width: 1023px) {
  .card__front h3 {
    padding: 28px 35px;
  }
}
@media (max-width: 767px) {
  .card__front h3 {
    padding: 18px 25px;
  }
}

.card__back {
  padding: 28px 25px;
  background-color: #011c40;
  overflow-y: auto;
  transform: rotateY(-180deg);
}
@media (max-width: 1023px) {
  .card__back {
    padding: 28px 35px;
  }
}
@media (max-width: 767px) {
  .card__back {
    padding: 28px 25px;
  }
}

.card__back h3 {
  margin: 0 0 15px 0;
}

.card__back li {
  margin: 0 0 12px 0;
}

.card__back p {
  margin: 0;
  font-size: 14px;
  line-height: 19.6px;
}

.card__back p:first-child {
  margin: 0 0 1px 0;
  font-weight: 500;
}

.card__back a {
  display: block;
  padding: 9px;
  width: 100%;
  min-height: 40px;
  text-align: center;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  background-color: #f9fbfd;
  color: #011c40;
  user-select: none;
}
@media (hover: hover) {
  .card__back a:hover:not(.focus-visible) {
    box-shadow: 0 0 10px 1px #f9fbfd;
  }
}
.card__back a.focus-visible:focus {
  outline: none;
  box-shadow: 0 0 10px 1px #f9fbfd;
}
.card__back a:active {
  opacity: 94%;
}

.catalog__wrapper {
  margin: 0 auto;
  padding: 85px 0 120px 0;
  max-width: 1130px;
}
@media (max-width: 1023px) {
