  margin: 0 0 49px 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  text-align: center;
}
@media (max-width: 1023px) {
  .catalog__wrapper h2 {
    font-size: 26px;
    line-height: 39px;
  }
}
@media (max-width: 767px) {
