.custom-input {
  position: relative;
}
.custom-input.is-invalid input {
  outline: 1px solid #ff1553;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover {
  outline: 1.5px solid #ff1553;
}
.custom-input .input-message {
  position: absolute;
  top: -15px;
  left: 0;
}
.custom-input .input-message.is-invalid {
  font-size: 11px;
  line-height: 12px;
  color: #f9fbfd;
}

.footer {
  background-color: #011c40;
}

.footer__wrapper {
  display: grid;
  grid-template-columns: 70px auto auto;
  gap: 40px;
  align-items: center;
  margin: 0 auto;
  padding: 49px 0;
  max-width: 1160px;
}
